import org.jetbrains.compose.web.css.*

data class Review(
    val userName: String,
    val rating: Int, // 1-5
    val reviewText: String
)

val ReviewCardStyle = CssStyle.base {
    Modifier
        .backgroundColor(Colors.White)
        .borderRadius(16.px)
        .padding(20.px)
        .styleModifier {
            property("box-shadow", "0 4px 20px rgba(0, 0, 0, 0.08)")
            property("border", "1px solid #f0f0f0")
        }
}

val ReviewsContainerStyle = CssStyle.base {
    Modifier
        .display(DisplayStyle.Grid)
        .styleModifier {
            property("grid-template-columns", "repeat(auto-fit, minmax(318px, 1fr))")
            property("gap", "16px")
        }
}

@Composable
fun StarRating(
    rating: Int,
    maxRating: Int = 5,
    starSize: CSSLengthValue = 16.px,
    modifier: Modifier = Modifier
) {
    Row(
        horizontalArrangement = Arrangement.spacedBy(2.px),
        verticalAlignment = Alignment.CenterVertically,
        modifier = modifier
    ) {
        repeat(maxRating) { index ->
            val isFilled = index < rating
            SpanText(
                "",
                modifier = Modifier
                    .fontSize(starSize)
                    .color(if (isFilled) rgb(255, 193, 7) else rgb(224, 224, 224))
            )
        }
    }
}


@Composable
fun ReviewsSection(
    reviews: List<Review>,
    modifier: Modifier = Modifier
) {
    Column(
        modifier = modifier,
        verticalArrangement = Arrangement.spacedBy(28.px),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {

        // Reviews grid
        Column(
            modifier = ReviewsContainerStyle.toModifier().then(Modifier.width(100.percent))
        ) {
            reviews.forEach { review ->
                CompactReviewCard(review = review)
            }
        }
    }
}

@Composable
fun ReviewCard(
    review: Review,
    modifier: Modifier = Modifier
) {
    Column(
        modifier = ReviewCardStyle.toModifier().then(modifier),
        verticalArrangement = Arrangement.spacedBy(12.px)
    ) {
        // Header with user info and rating
        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.SpaceBetween,
            modifier = Modifier.fillMaxWidth()
        ) {
            Row(
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.spacedBy(12.px)
            ) {
                // User avatar - generated from initials
                val initials = review.userName.split(" ").mapNotNull { it.firstOrNull() }.take(2).joinToString("")
                Column(
                    modifier = Modifier
                        .size(32.px)
                        .borderRadius(50.percent)
                        .backgroundColor(rgb(66, 133, 244))
                        .color(Colors.White),
                    horizontalAlignment = Alignment.CenterHorizontally,
                    verticalArrangement = Arrangement.Center
                ) {
                    SpanText(
                        initials,
                        modifier = Modifier
                            .fontSize(16.px)
                            .fontWeight(FontWeight.SemiBold)
                    )
                }

                SpanText(
                    review.userName,
                    modifier = Modifier
                        .fontSize(14.px)
                        .fontWeight(FontWeight.SemiBold)
                        .color(rgb(51, 51, 51))
                )
            }

            StarRating(rating = review.rating)
        }

        // Review text
        SpanText(
            review.reviewText,
            modifier = Modifier
                .fontSize(14.px)
                .lineHeight(1.5)
                .color(rgb(85, 85, 85))
        )

        // Google Play badge
        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.spacedBy(6.px),
            modifier = Modifier//.alignSelf(AlignSelf.Start)
        ) {
            Image(
                src = "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='%234285f4'%3E%3Cpath d='M3,20.5V3.5C3,2.91 3.34,2.39 3.84,2.15L13.69,12L3.84,21.85C3.34,21.6 3,21.09 3,20.5M16.81,15.12L6.05,21.34L14.54,12.85L16.81,15.12M20.16,10.81C20.5,11.08 20.75,11.5 20.75,12C20.75,12.5 20.53,12.9 20.18,13.18L17.89,14.5L15.39,12L17.89,9.5L20.16,10.81M6.05,2.66L16.81,8.88L14.54,11.15L6.05,2.66Z'/%3E%3C/svg%3E",
                alt = "Google Play",
                modifier = Modifier.size(16.px)
            )
            SpanText(
                "Google Play",
                modifier = Modifier
                    .fontSize(12.px)
                    .color(rgb(66, 133, 244))
                    .fontWeight(FontWeight.Medium)
            )
        }
    }
}

@Composable
fun CompactReviewCard(
    review: Review,
    modifier: Modifier = Modifier
) {
    Row(
        modifier = ReviewCardStyle.toModifier()
            .then(Modifier.padding(16.px))
            .then(modifier),
        horizontalArrangement = Arrangement.spacedBy(12.px),
        verticalAlignment = Alignment.Top
    ) {

        Column(
            verticalArrangement = Arrangement.spacedBy(6.px),
            modifier = Modifier.flexGrow(1)
        ) {

            Row(
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.SpaceBetween,
                modifier = Modifier.fillMaxWidth()
            ) {
                Row(
                    verticalAlignment = Alignment.CenterVertically,
                    horizontalArrangement = Arrangement.spacedBy(4.px)
                ) {

                    // User avatar - generated from initials
                    val initials = review.userName.split(" ").mapNotNull { it.firstOrNull() }.take(2).joinToString("")
                    Column(
                        modifier = Modifier
                            .size(24.px)
                            .borderRadius(100.percent)
                            .backgroundColor(rgb(66, 133, 244))
                            .color(Colors.White),
                        horizontalAlignment = Alignment.CenterHorizontally,
                        verticalArrangement = Arrangement.Center
                    ) {
                        SpanText(
                            initials,
                            modifier = Modifier
                                .fontSize(8.px)
                                .fontWeight(FontWeight.SemiBold)
                        )
                    }
                    SpanText(
                        review.userName,
                        modifier = Modifier
                            .fontSize(13.px)
                            .fontWeight(FontWeight.SemiBold)
                            .color(rgb(51, 51, 51))
                    )
                }

                Row(
                    verticalAlignment = Alignment.CenterVertically,
                    horizontalArrangement = Arrangement.spacedBy(4.px)
                ) {
                    StarRating(rating = review.rating, starSize = 14.px)

                    // google play icon
                    Image(
                        src = "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='%234285f4'%3E%3Cpath d='M3,20.5V3.5C3,2.91 3.34,2.39 3.84,2.15L13.69,12L3.84,21.85C3.34,21.6 3,21.09 3,20.5M16.81,15.12L6.05,21.34L14.54,12.85L16.81,15.12M20.16,10.81C20.5,11.08 20.75,11.5 20.75,12C20.75,12.5 20.53,12.9 20.18,13.18L17.89,14.5L15.39,12L17.89,9.5L20.16,10.81M6.05,2.66L16.81,8.88L14.54,11.15L6.05,2.66Z'/%3E%3C/svg%3E",
                        alt = "Google Play",
                        modifier = Modifier.size(16.px)
                    )
                }


            }

            SpanText(
                review.reviewText,
                modifier = Modifier
                    .fontSize(13.px)
                    .lineHeight(1.4)
                    .color(rgb(85, 85, 85))
            )
        }
    }
}

