import androidx.compose.runtime.Composable
import androidx.compose.runtime.ProvidableCompositionLocal
import androidx.compose.runtime.compositionLocalOf
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlin.Any
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable
import com.neonbits.shweyetswel.components.layouts.MarkdownLayout as _com_neonbits_shweyetswel_components_layouts_MarkdownLayout
import com.neonbits.shweyetswel.components.layouts.PageLayout as _com_neonbits_shweyetswel_components_layouts_PageLayout
import com.neonbits.shweyetswel.pages.AboutPage as _com_neonbits_shweyetswel_pages_AboutPage
import com.neonbits.shweyetswel.pages.HomePage as _com_neonbits_shweyetswel_pages_HomePage

private val LayoutScopeLocal: ProvidableCompositionLocal<Any?> = compositionLocalOf<Any?> { null }

@Composable
private fun <T : Any> currentLayoutScope(): T {
    @Suppress("UNCHECKED_CAST")
    return LayoutScopeLocal.current as? T
        ?: error("Unexpected between Page/Layout scope and the parent Layout which it is providing it. Please report this issue to the Kobweb developers.")
}

@Composable
private fun <T : Any> provideLayoutScope(layoutScope: T, content: @Composable () -> Unit) {
    androidx.compose.runtime.CompositionLocalProvider(LayoutScopeLocal provides layoutScope, content = content)
}

public fun main() {
    AppGlobals.initialize(mapOf("title" to "ShweYetSwel"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.registerLayout(
            "com.neonbits.shweyetswel.components.layouts.MarkdownLayout",
            parentLayoutId = "com.neonbits.shweyetswel.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.neonbits.shweyetswel.components.layouts.initMarkdownLayout(ctx)
            },
        ) { pageCtx, pageMethod -> 
            _com_neonbits_shweyetswel_components_layouts_MarkdownLayout {
                pageMethod(pageCtx)
            }
        }
        ctx.router.registerLayout(
            "com.neonbits.shweyetswel.components.layouts.PageLayout",
        ) { pageCtx, pageMethod -> 
            _com_neonbits_shweyetswel_components_layouts_PageLayout(pageCtx) {
                provideLayoutScope(this) {
                    pageMethod(pageCtx)
                }
            }
        }
        ctx.router.register(
            "/",
            layoutId = "com.neonbits.shweyetswel.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                com.neonbits.shweyetswel.pages.initHomePage(ctx)
            },
        ) { pageCtx -> 
            _com_neonbits_shweyetswel_pages_HomePage()
        }
        ctx.router.register(
            "/about",
            layoutId = "com.neonbits.shweyetswel.components.layouts.MarkdownLayout",
            initRouteMethod = { ctx ->
                com.neonbits.shweyetswel.pages.initAboutPage(ctx)
            },
        ) { pageCtx -> 
            _com_neonbits_shweyetswel_pages_AboutPage()
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text", com.neonbits.shweyetswel.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text", com.neonbits.shweyetswel.SubheadlineTextStyle)
        ctx.theme.registerStyle("footer", com.neonbits.shweyetswel.components.sections.FooterStyle)
        ctx.theme.registerStyle("nav-header", com.neonbits.shweyetswel.components.sections.NavHeaderStyle)
        ctx.theme.registerStyle("markdown", com.neonbits.shweyetswel.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle("page-content", com.neonbits.shweyetswel.components.layouts.PageContentStyle)
        ctx.theme.registerStyle("download-button", com.neonbits.shweyetswel.components.layouts.DownloadButtonStyle)
        ctx.theme.registerStyle("play-store-button", com.neonbits.shweyetswel.components.layouts.PlayStoreButtonStyle)
        ctx.theme.registerStyle("review-card", com.neonbits.shweyetswel.components.ReviewCardStyle)
        ctx.theme.registerStyle("reviews-container", com.neonbits.shweyetswel.components.ReviewsContainerStyle)
        ctx.theme.registerStyle("hero-container", com.neonbits.shweyetswel.pages.HeroContainerStyle)
        ctx.theme.registerStyle("home-grid", com.neonbits.shweyetswel.pages.HomeGridStyle)
        ctx.theme.registerStyle("home-grid-cell", com.neonbits.shweyetswel.pages.HomeGridCellStyle)
        ctx.theme.registerVariant("-circle", com.neonbits.shweyetswel.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", com.neonbits.shweyetswel.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("side-menu-slide-in", com.neonbits.shweyetswel.components.sections.SideMenuSlideInAnim)
        com.neonbits.shweyetswel.initSiteStyles(ctx)
        com.neonbits.shweyetswel.initColorMode(ctx)
        com.neonbits.shweyetswel.initStyles(ctx)
        com.neonbits.shweyetswel.initTheme(ctx)
    }

    // Dedup any leading slashes after removing the origin, just in case someone typed
    // something like `https://site.com//about` by accident. If we pass `//about` into
    // `tryRoutingTo`, Kobweb will reject it as a protocol-relative URL; instead, we
    // want it to navigate to `/about`
    router.tryRoutingTo("/" + BasePath.remove(window.location.href.removePrefix(window.origin).trimStart('/')), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        com.neonbits.shweyetswel.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
