// Container that has a tagline and grid on desktop, and just the tagline on mobile
val HeroContainerStyle = CssStyle {
    base { Modifier.fillMaxWidth().alignContent(alignContent = AlignContent.Center).gap(2.cssRem) }
    Breakpoint.MD { Modifier.margin { top(2.vh) } }
}

// A demo grid that appears on the homepage because it looks good
val HomeGridStyle = CssStyle.base {
    Modifier
        .gap(0.5.cssRem)
        .width(70.cssRem)
        .height(18.cssRem)
}

private val GridCellColorVar by StyleVariable<Color>()
val HomeGridCellStyle = CssStyle.base {
    Modifier
        .backgroundColor(GridCellColorVar.value())
        .boxShadow(blurRadius = 0.6.cssRem, color = GridCellColorVar.value())
        .borderRadius(1.cssRem)
}


@InitRoute
fun initHomePage(ctx: InitRouteContext) {
    ctx.data.add(PageLayoutData("Home"))
}

@Page
@Layout(".components.layouts.PageLayout")
@Composable
fun HomePage() {
    Row(HeroContainerStyle.toModifier(), horizontalArrangement = Arrangement.Center) {
        Box {
            val sitePalette = ColorMode.current.toSitePalette()

            Column(Modifier.gap(2.cssRem)) {
                Div(HeadlineTextStyle.toAttrs()) {
                    SpanText(
                        "\n\n ", Modifier.color(
                            when (ColorMode.current) {
                                ColorMode.LIGHT -> Colors.Black
                                ColorMode.DARK -> Colors.White
                            }
                        )
                    )
                    SpanText(
                        "",
                        Modifier
                            .color(sitePalette.brand.accent)
                            // Use a shadow so this light-colored word is more visible in light mode
                            .textShadow(0.px, 0.px, blurRadius = 0.5.cssRem, color = Colors.Gray)
                    )
                }

                // Side by side buttons
                DownloadButtons(
                    appStoreUrl = "https://apps.apple.com/app/your-app/id123456789",
                    playStoreUrl = "https://play.google.com/store/apps/details?id=com.yourapp.package"
                )

            }
        }

        Div(
            HomeGridStyle
                .toModifier()
                .displayIfAtLeast(Breakpoint.MD)
                .padding(left = 22.cssRem)
                .toAttrs()
        ) {
            Image(
                "/homeScreenshot.png", "Home Screenshot",
                Modifier.height(32.cssRem)
                    .display(DisplayStyle.Block)
            )
        }

        // Full reviews section
        ReviewsSection(
            reviews = sampleReviews,
            modifier = Modifier.maxWidth(1200.px).displayIfAtLeast(Breakpoint.MD)
        )
    }
}
