@Composable
fun DownloadButtons(
    appStoreUrl: String,
    playStoreUrl: String,
    modifier: Modifier = Modifier
) {
    Row(
        horizontalArrangement = Arrangement.spacedBy(16.px),
        verticalAlignment = Alignment.CenterVertically,
        modifier = modifier
    ) {
        AppStoreButton(appStoreUrl)
        PlayStoreButton(playStoreUrl)
    }
}

// Alternative: Stacked layout for mobile
@Composable
fun DownloadButtonsStacked(
    appStoreUrl: String,
    playStoreUrl: String,
    modifier: Modifier = Modifier
) {
    Row(
        modifier = modifier.flexDirection(FlexDirection.Column).gap(12.px)
    ) {
        AppStoreButton(
            appStoreUrl = appStoreUrl,
            modifier = Modifier.width(200.px)
        )
        PlayStoreButton(
            playStoreUrl = playStoreUrl,
            modifier = Modifier.width(200.px)
        )
    }
}
